                   2.ÂØπ‰∏ãËΩΩÁöÑÊñá‰ª∂‰∏çÂÅö‰ªª‰Ωï‰øÆÊîπÔºåÂπ∂‰∏î‰∏ãËΩΩ2Â∞èÊó∂ÂêéÊúçÂä°Âô®‰ºöËá™Âä®Â∞ÜÂÖ∂Âà†Èô§ÔºåËØ∑ÂèäÊó∂‰∏ãËΩΩÂà∞Êú¨Âú∞„ÄÇ
                   3.Áî±‰∫éÊúçÂä°Âô®Á£ÅÁõòÂÆπÈáèÊúâÈôêÔºåËØ∑‰∏çË¶Å‰∏ãËΩΩËøáÂ§ß‰∏î‰∏éÂ∑•‰Ωú„ÄÅÂ≠¶‰π†Êó†ÂÖ≥ÁöÑËµÑÊñô„ÄÇ
          </pre>
