<script>
import Clipboard from "clipboard";

// import * as d3 from 'd3'

export default {

data() {

    return {
      //Áî®Êà∑ËæìÂÖ•url
      userURL: "",
      //card ÊòØÂê¶ÊòæÁ§∫
      CardShow: false,
      loading: false,
      downloadURL: "",
      filename: "",
      md5: "",
      size: "",
      sizeunit: "",
      imgShow: false,
      RecentTableShow: true, //ÊúÄËøë‰∏ãËΩΩÊñá‰ª∂ÂàóË°®Ë°®Ê†ºÊòØÂê¶ÊòæÁ§∫
      fileListdata:[],
      QueryIP: "",
      GeoIPContry: "",
      GeoIPSubCity: "",
      GeoIPCityName: "",
      drawer: false,
      direction: 'btt',
      last30aydlDate:[],
      last30aydlCount:[]

    }
  },
//ÂàùÂßãÂåñÈ°µÈù¢ÔºåËé∑ÂèñÊúÄËøë‰∏ãËΩΩÊñá‰ª∂ÂàóË°®‰ø°ÊÅØ
created () {
    this.getRentFileList()
    this.getMyIPInfo()
    this.getLast30DayDl()
},
methods: {

    //Ê∏ÖÈô§‰∏ãËΩΩÊï∞ÊçÆ
    restData() {
      this.CardShow = false
      this.downloadURL = ""
      this.filename = ""
      this.md5 = ""
      this.size = ""
      this.sizeunit = ""
    },
    //ÊòæÁ§∫Êî∂Ê¨æ‰∫åÁª¥Á†ÅÁ™óÂè£
    toDonate(){

        //this.imgShow = true
        if(this.imgShow){
						this.imgShow=false
					}else{
						// this.imgShow=true
            this.$alert('<img align="center" height="150px" src="https://static.chromiumer.com/donate.jpg">', '', {
              dangerouslyUseHTMLString: true,
              confirmButtonText: "Thanks for your donationüôè",
              center: true,
              showClose: true
            });
				}
      },
    //ÊâßË°å‰∏ãËΩΩÊìç‰Ωú
    DownloadRes() {

      const regURL = /^http[s]{0,1}:\/\/([\w.]+\/?)\S*/

      if(!regURL.test(this.userURL)) {

            // return this.$message.error('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑËµÑÊ∫êURL!!')
            return this.$notify.success({
              title: 'ÁúãÊàëÂ§ßÁôΩÁúºüôÑ!!',
              message: 'ÈìÅÊ±ÅÂÑøÔºåÂæóËæìÂÖ•Ê≠£ÁªèÁöÑURLÂìáÔºÅÔºÅÔºÅ',
              position: 'top-left',
              showClose: false
            });

      }else if( this.userURL === '' ) {

            return this.$message.success('Please enter the resources URL You want to download !!')

      }else {

            this.loading = true

            this.$http.get('/dl?url=' + this.userURL ).then(res => {

                if (res.status !== 200 ) {

                    return this.$message.error('Resources Request Failed, Please try again later !!! ')

                }else{

                    this.downloadURL =  res.data.dlurl
                    this.filename =  res.data.filename
                    this.md5 =  res.data.md5
                    this.size =  res.data.size
                    this.sizeunit =  res.data.sizeunit

                    this.RecentTableShow = false //ÂÖ≥Èó≠ÊúÄËøë‰∏ãËΩΩË°®Ê†ºÊòæÁ§∫
                    this.loading = false //ÂÖ≥Èó≠ÈÅÆÁΩ©ÊòæÁ§∫
                    this.CardShow = true //ÊòæÁ§∫‰∏ãËΩΩÁöÑÊñá‰ª∂‰ø°ÊÅØÂç°Áâá

                    // Ëá™Âä®‰∏ãËΩΩ
                    // this.$http({
                    //   url: this.downloadURL,
                    //   method: 'GET',
                    //   responseType: 'blob', // important
                    // }).then((response) => {
                    //   const url = window.URL.createObjectURL(new Blob([response.data]));
                    //   const link = document.createElement('a');
                    //   link.href = url;
                    //   link.setAttribute('download', res.data.filename);  //‰∏ãËΩΩÂà∞Êú¨Âú∞ÁöÑÊñá‰ª∂ÂêçÁß∞
                    //   document.body.appendChild(link);
                    //   link.click();
                    // });

              }
          })

        }
      },
      getRentFileList(){

            this.$http.get('/filelist').then(res => {
                //console.log(res.data.length)
                if (res.status !== 200) { 
                    return this.$message.error('Êñá‰ª∂‰ø°ÊÅØËé∑ÂèñÂ§±Ë¥•ÔºÅ')
                }else{

                    this.fileListdata = res.data
                    // console.log(res.data)
                }
            })
      },
      //Ëé∑ÂèñipÂú∞ÁêÜ‰ΩçÁΩÆ
      getMyIPInfo(){

            this.$http.get('https://api.chromiumer.com' ).then(res => {

                if (res.status !== 200 ) {

                    return this.$message.error('Resources Request Failed, Please try again later !!! ')

                }else{

                  // console.log(res.data.GEOIPContry,res.data.GEOIPSubdivisionsCity,res.data.GEOIPCityName,res.data.Query)

                  this.GeoIPContry = res.data.GEOIPContry
                  this.GeoIPSubCity = res.data.GEOIPSubdivisionsCity
                  this.GeoIPCityName = res.data.GEOIPCityName
                  this.QueryIP = res.data.Query
                  // console.log(res.data)

              }
          })
      },
      copyUrl() {
          let _this = this
          let clipboard = new Clipboard("#copyBtn");
          clipboard.on("success", function() {
              // console.log("Â§çÂà∂ÊàêÂäü");
              clipboard.destroy();
              return _this.$message.success("Copy SuccessÔºÅ ")
          });
      },
      getLast30DayDl(){

      // ÈúÄË¶ÅÂÖàÊ∏ÖÁ©∫Êï∞ÊçÆÔºåÈÅøÂÖçÊï∞ÊçÆËøΩÂä†Â§öÊ¨°„ÄÇ
      this.last30aydlDate = []
      this.last30aydlCount = []

      // Ëé∑ÂèñÊúÄËøë30Âπ¥‰∏ãËΩΩÁªüËÆ°Êï∞ÊçÆ
      this.$http.get('last30aydl').then(res => {

            if (res.status !== 200) { 
                        this.$message.error("Êï∞ÊçÆËØ∑Ê±ÇÂ§±Ë¥•„ÄÇ")
            }else{

                // ÁªÑË£ÖÊï∞ÊçÆ
                console.log(res.data.length)
                for (var i = 0; i < res.data.length; i++){
                  // console.log(res.data[i].Datetimes,res.data[i].Counts)
                  this.last30aydlDate.push(
                    res.data[i].Datetimes
                  )
                  this.last30aydlCount.push(
                    res.data[i].Counts
                  )
                }
            // chat option start
            // ÂõæË°®Â±ïÁ§∫
            var echarts1 = require('echarts');
            var myChart1 = echarts1.init(document.getElementById('myChart1'));
            myChart1.setOption({
                          color:['#409EFF'],
                          tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                              type: 'shadow'
                            }
                          },
                          xAxis: {
                            type: 'category',
                            data: this.last30aydlDate
                          },
                          yAxis: {
                            type: 'value'
                          },
                          series: [
                            {
                              name: '‰∏ãËΩΩÊ¨°Êï∞',
                              data: this.last30aydlCount,
                              animationDelay: function(idx) { return idx * 10; },                              
                              type: 'bar',
                              smooth: true,
                              // animationEasing: 'elasticOut',
                              animationDelayUpdate: function(idx) { return idx * 10; },
                              label:{
                                show: true,
                                position: 'top'
                              }
                            }
                          ]

                          })
            // chat option end

          }
      }).catch(error => console.log(error))

      },
      opens(){
        this.$nextTick(()=>{
            this.getLast30DayDl();
        });
      }

    }
  }
</script>

